.converter {
  padding: 10% 0;
}

.converter__text {
  margin: 0 0 20px;
  font-size: 16px;
  font-weight: 400;
}

.converter__text:first-of-type {
  margin-bottom: 4px;
}

.converter__text_theme_marked {
  color: #f72;
  font-size: 12px;
}

.converter__text-marked {
  font-size: 14px;
  font-weight: 500;
  font-style: italic;
}

.converter__note {
  display: flex;
  align-items: flex-start;
}

.converter__note-list {
  margin: 0;
  padding-left: 30px;
}

.converter__form {
  width: 100%;
  margin-top: 40px;
  display: flex;
  gap: 10px;
}

.converter__input {
  width: 100%;
  height: 32px;
  border: 1px solid #383838;
  border-radius: 4px;
  background-color: #fff;
  box-sizing: border-box;
}

.converter__input::placeholder {
  color: #222;
  font-size: 14px;
  font-style: italic;
}

.converter__input:focus {
  border: none;
  outline: 2px solid #4040ff;
}

.converter__button {
  height: 32px;
  border: none;
  border-radius: 4px;
  padding: 0 10px;
  box-sizing: border-box;
  background-color: #4040ff;
  transition: opacity .2s;
  color: #fff;
  font-size: 16px;
  font-weight: 500;
}

.converter__button:hover {
  cursor: pointer;
  opacity: .8;
}

.converter__button_inactive {
  opacity: .5;
}

.converter__button_inactive:hover {
  cursor: default;
  opacity: .5;
}

.converter__result {
  max-width: 400px;
  margin: 80px auto 0;
  text-align: center;
  font-size: 36px;
  font-weight: 500;
}

@media screen and (max-width: 480px) {
  .converter__text {
    font-size: 14px;
  }

  .converter__text_theme_marked {
    font-size: 12px;
  }

  .converter__text-marked {
    font-size: 13px;
  }

  .converter__note-list {
    padding-left: 20px;
  }

  .converter__form {
    flex-direction: column;
    gap: 20px;
  }

  .converter__button {
    height: 40px;
  }

  .converter__result {
    margin: 40px auto 0;
    font-size: 24px;
  }
}
